import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Helmet from 'react-helmet'
import axios from 'axios'
import Layout from '../../components/Layout/Layout'
import {
    Card,
    CardActionArea,
    CardContent,
    CardMedia,
    createStyles,
    Grid,
    makeStyles,
    Typography,
} from '@material-ui/core'
import Welcome from '../../components/Welcome'
import moment from 'moment'
moment().format()

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        root: {
            flexGrow: 1,
            marginTop: '20px',
        },
        media: {
            height: 0,
            paddingTop: '56.25%', // 16:9\
            backgroundSize: 'contain',
        },
    })
)

const Blog = props => {
    const classes = useStyles()
    const [posts, setPosts] = useState([])
    const [title, setTitle] = useState('')
    useEffect(() => {
        axios
            .post('https://addspire-blog.herokuapp.com/graphql', {
                query: `
            query {
  posts (where: { language:"${
      props.history.location.pathname.indexOf('/ru') === 0 ? 'ru' : 'en'
  }" }, sort:"published_at:desc"){
    url
    title
    published_at
   previewImage {
    url
  }
    subtitle
  }
}`,
            })
            .then(res => {
                setPosts(res.data.data.posts)
                setTitle(
                    'Addspire Blog - a social network for sharing and supporting ideas, promises and resolutions.'
                )
            })
            .catch(err => {})
    }, [props.history.location.pathname])
    return (
        <>
            {title && (
                <Helmet>
                    <title>{title}</title>
                </Helmet>
            )}
            <Layout
                header={
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                        noWrap
                    >
                        {title}
                    </Typography>
                }
            >
                <Welcome />
                <div className={classes.root}>
                    <Grid container spacing={3}>
                        {posts.map(post => (
                            <Grid item xs={12} md={6} xl={4}>
                                <Card>
                                    <Link
                                        to={
                                            props.history.location.pathname +
                                            '/' +
                                            post.url.split('.')[0]
                                        }
                                    >
                                        <CardActionArea>
                                            <CardMedia
                                                className={classes.media}
                                                image={
                                                    post.previewImage &&
                                                    post.previewImage.url
                                                }
                                                title={post.title}
                                            />
                                            <CardContent>
                                                <Typography
                                                    gutterBottom
                                                    variant="h5"
                                                    component="h2"
                                                >
                                                    {post.title}
                                                </Typography>
                                                <Typography
                                                    gutterBottom
                                                    variant="subtitle1"
                                                >
                                                    {post.subtitle}
                                                </Typography>

                                                <Typography
                                                    align="right"
                                                    variant="button"
                                                    color="textSecondary"
                                                    component="p"
                                                >
                                                    {moment(
                                                        post.published_at
                                                    ).format('MMMM Do[,] YYYY')}
                                                </Typography>
                                            </CardContent>
                                        </CardActionArea>
                                    </Link>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </div>
            </Layout>
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Blog)
)
